/**
 * @file 对话框相关mixin
 */

export default {

    methods: {
        msg(content) {
            this.$message.success(content)
        },

        alert(content, callback) {
            this.$alert(content, '提示').then(() => {
                if (typeof(callback) === 'function') {
                    callback()
                }
            }).catch(() => {
                if (typeof(callback) === 'function') {
                    callback()
                }
            })
        },

        confirm(content, callback) {
            this.$confirm(content, '询问').then(() => {
                if (typeof(callback) === 'function') {
                    callback()
                }
            }).catch(() => {
            })
        },

        prompt(content, callback) {
            this.$prompt(content, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputPattern: /.+/,
                inputErrorMessage: '请输入'
            }).then(({value}) => {
                callback(value)
            }).catch(() => {

            })
        }
    }

}