/**
 * @file 主文件
 **/
import Vue from 'vue'
import VueRouter from 'vue-router'
import {APP_TITLE} from './conf/app'
import RouterConf from './conf/router'
import {ROOT_PATH} from './conf/urls'
import Filters from './lib/filters'
import Store from './store'
import RouterView from './view/layout/router.vue'
import globalComponents from './conf/global'
import _ from 'lodash'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import {api} from './lib/request'
import {API_COMMON_BASE} from './conf/urls'
import * as types from './store/mutation-types'

function showError(msg) {
    document.getElementById('app').innerHTML = `<div style="padding: 24px">${msg}</div>`
}

// 加载饿了么组件库
Vue.use(ElementUI)

// 路由处理
Vue.use(VueRouter)
const router = new VueRouter({
    base: ROOT_PATH + '/',
    mode: 'pushState' in history ? 'history' : 'hash',
    routes: RouterConf
})
router.beforeEach((to, from, next) => {
    let title = APP_TITLE
    if (to.meta && to.meta.title) {
        title += ' - ' + to.meta.title
    }
    document.title = title
    window.scrollTo(0, 0)
    next()
})

// 初始化全局过滤器、组件、mixin
_.each(Filters, (filter, name) => Vue.filter(name, filter))
_.each(globalComponents['components'], (comp, name) => Vue.component(name, comp))
_.each(globalComponents['mixins'], mixin => Vue.mixin(mixin))


// 获取基础数据并挂载app
api({
    url: API_COMMON_BASE,
    success(data) {
        const app = new Vue({
            router,
            store: Store,
            render: h => h(RouterView),
            beforeCreate() {
                if (data.login) {
                    this.$store.commit(types.UPDATE_LOGIN_BASE_DATA, {
                        name: data.user.name,
                        auth: data.user.auth.split('').map(d => parseInt(d, 10)),
                    })
                } else {
                    this.$router.push('/login')
                }
            }
        })
        app.$mount('#app')
    },
    error(msg) {
        showError(msg)
    }
})