/**
 * @file 基本信息
 *
 */
import * as types from '../mutation-types'


export default {
    state: {
        name: '',
        auth: []
    },

    getters: {
        baseInfoState: state => state
    },

    actions: {

    },

    mutations: {
        [types.UPDATE_LOGIN_BASE_DATA](state, {name, auth}) {
            state.name = name
            state.auth = auth
        }
    }
}