/**
 * @file 事件注册库
 */

const type = (window.addEventListener ? 1 : 0)

export function addEvent(obj, evt, listener) {
    if (type) {
        obj.addEventListener(evt, listener, false)
    } else {
        obj.attachEvent('on' + evt, listener)
    }
}

export function removeEvent(obj, evt, listener) {
    if (type) {
        obj.removeEventListener(evt, listener, false)
    } else {
        obj.detachEvent('on' + evt, listener)
    }
}