<template>
    <i style="display:none"></i>
</template>

<script>
    /**
     * @file 事件管理组件
     */
    import {addEvent, removeEvent} from '../../lib/event'

    export default {
        props: {
            dom: {
                required: false,
                default: () => window
            },

            trigger: {
                type: [Boolean],
                required: false,
                default: false
            },

            lazy: {
                type: Number,
                default: -1
            }
        },

        data() {
            return {
                timer: null
            }
        },

        created() {
            const events = {}
            for (let key in this._events) {
                events[key] = e => {
                    if (this.lazy < 0) {
                        this.$emit(key, e)
                    } else {
                        if (this.timer !== null) {
                            clearTimeout(this.timer)
                            this.timer = null
                        }
                        this.timer = setTimeout(() => {
                            this.$emit(key, e)
                            this.timer = null
                        }, this.lazy)
                    }
                }
                addEvent(this.dom, key, events[key])
            }
            this.eventsHandlers = events
        },

        mounted() {
            if (this.trigger === true) {
                for (let key in this.eventsHandlers) {
                    this.eventsHandlers[key]()
                }
            }
        },

        destroyed() {
            for (let key in this.eventsHandlers) {
                removeEvent(this.dom, key, this.eventsHandlers[key])
            }
        }
    }
</script>