<style lang="less"></style>

<template>
    <el-dialog title="修改密码" :visible="open" width="500px" :before-close="onClose">
        <el-form :rules="rules" ref="form" :model="form" label-width="90px" size="small">
            <el-form-item label="旧密码" prop="old">
                <el-input type="password" v-model.trim="form.old"></el-input>
            </el-form-item>
            <el-form-item label="新密码" prop="password">
                <el-input type="password" v-model.trim="form.password"></el-input>
            </el-form-item>
            <el-form-item label="重复密码" prop="password2">
                <el-input type="password" v-model.trim="form.password2"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="open = false">取 消</el-button>
            <el-button type="primary" @click="onSubmit">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    export default {
        computed: {
            rules: () => ({
                old: [
                    {required: true, message: '请输入旧密码', trigger: 'blur'}
                ],
                password: [
                    {required: true, message: '请输入新密码', trigger: 'blur'},
                    {min: 6, max: 200, message: '长度在 6 到 200 个字符', trigger: 'blur'}
                ],
                password2: [
                    {required: true, message: '请输入再输入一次新密码', trigger: 'blur'},
                    {min: 6, max: 200, message: '长度在 6 到 200 个字符', trigger: 'blur'}
                ]
            })
        },

        data() {
            return {
                open: false,
                form: {
                    old: '',
                    password: '',
                    password2: '',
                },
                callback: null
            }
        },

        mounted() {

        },

        methods: {
            showDialog() {
                this.open = true
                this.form = {old: '', password: ''}
            },

            onClose() {
                this.open = false
            },

            onSubmit() {
                this.$refs['form'].validate(valid => {
                    if (!valid) {
                        return
                    }
                    if (this.form.password !== this.form.password2) {
                        return this.alert('两次输入的密码不一致')
                    }
                    this.$store.dispatch('changePassword', {
                        data: this.form,
                        success: data => {
                            if (data && data.success) {
                                this.alert('修改密码成功')
                                this.open = false
                            }
                        },
                        error: msg => {
                            this.alert(msg)
                        }
                    })
                })
            }
        }
    }
</script>