/**
 * @file store入口文件
 */

import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import * as getters from './getters'
import baseModule from './common/baseinfo'

Vue.use(Vuex)

export default new Vuex.Store({
    actions,
    getters,
    modules: {
        'baseinfo': baseModule,
    },
    strict: true,
    plugins: []
})