/**
 * @file filters
 */
import _ from 'lodash'
import {dateFormat} from './datetime'
import {formatPercent, formatFloat} from './number'

export function currency(value) {
    const number = parseFloat(value)
    if (isNaN(number)) {
        return value
    }
    const ret = number.toLocaleString()
    return ret.replace(/\..+/g, '')
}

export function valueToText(value, enums, key = 'text') {
    const valueString = value + ''
    const selected = _.find(enums, enumItem => {
        let enumValue = enumItem['value'] + ''
        return enumValue === valueString
    })
    return _.isNil(selected) ? null : selected[key]
}

export function enumToSelectOptions(enums, field = 'text') {
    const options = _.map(enums, item => ({
        text: item[field],
        value: item['value'],
        key: item['value'],
        index: item['index']
    }))
    let sortField = 'value'
    if (options.length > 0 && !_.isNil(options[0]['index'])) {
        sortField = 'index'
    }
    return _.sortBy(options, sortField)
}

export function timestampToDateString(value, format = 'yyyy-MM-dd hh:mm:ss') {
    const dt = new Date(value)
    if (isNaN(dt.getTime())) {
        return ''
    }
    return dateFormat(dt, format)
}

export function fileSize(value) {
    if (value === null || value === undefined) {
        return ''
    }
    if (isNaN(value)) {
        return value
    }
    const bit = 1024
    const bit2 = 1024 * 1024
    const bit3 = 1024 * 1024 * 1024
    const fmt = num => {
        return Math.round(num * 100) / 100
    }
    if (value < bit) {
        return `${value}K`
    } else if (value < bit2) {
        return `${fmt(value / bit)}M`
    } else if (value < bit3) {
        return `${fmt(value / bit2)}G`
    }
    return `${fmt(value / bit3)}T`
}

export default {
    currency,
    valueToText,
    enumToSelectOptions,
    timestampToDateString,
    fileSize,
    formatPercent,
    formatFloat
}