<style lang="less" scoped>
    .app-page {
        margin: 20px 0;
        text-align: center;
        user-select: none;
        white-space: nowrap;

        a {
            display: inline-block;
            padding: 0 4px;
            font-size: 13px;
            height: 28px;
            line-height: 28px;
            box-sizing: border-box;
            margin: 0 4px;
            background-color: #fff;
            color: #606266;
            min-width: 30px;
            border-radius: 2px;
            vertical-align: middle;
            cursor: pointer;
            font-weight: 500;
            text-align: center;
        }

        .app-page-disabled {
            background-color: transparent;
            cursor: default;
            color: #c0c4cc;
        }

        .app-page-prev, .app-page-next {
            font-size: 15px;
            background-color: transparent;
            line-height: 26px;
        }

        .app-page-active {
            background-color: #409EFF;
            color: #fff !important;
        }

        span {
            vertical-align: middle;
            height: 28px;
            line-height: 28px;
            display: inline-block;
            margin: 0 20px;
            font-weight: 400;
            color: #606266;

            input {
                -webkit-appearance: none;
                background: none #fff;
                border-radius: 4px;
                border: 1px solid #dcdfe6;
                box-sizing: border-box;
                color: #606266;
                display: inline-block;
                font-size: inherit;
                line-height: 1;
                outline: 0;
                transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
                width: 46px;
                height: 28px;
                text-align: center;
                margin: 0 3px;

                &:hover {
                    border-color: #c0c4cc;
                }

                &:focus {
                    border-color: #409EFF;
                }
            }
        }
    }
</style>

<template>

    <div v-if="totalPage > 1" class="app-page">

        <span v-if="!isNaN(count) && count > 0">共{{count}}条记录</span>

        <a class="app-page-prev" :class="page <= 1 ? 'app-page-disabled' : ''" @click="onPageChange(page - 1)">
            <i class="fa fa-angle-left"></i>
        </a>

        <template v-for="i in totalPage" v-if="i === 1 || i === totalPage || (i >= info.start && i <= info.end)">
            <a :key="i" v-if="i === totalPage && info.end < totalPage - 1" class="app-page-disabled">...</a>
            <a :key="i" :class="i === page ? 'app-page-active' : ''" @click="onPageChange(i)">{{i}}</a>
            <a :key="i" v-if="i === 1 && info.start > 2" class="app-page-disabled">...</a>
        </template>

        <a class="app-page-next" :class="page >= totalPage ? 'app-page-disabled' : ''" @click="onPageChange(page + 1)">
            <i class="fa fa-angle-right"></i>
        </a>

        <span>
            前往<input type="text" :value="page" @blur="changeInputPage" @keyup.enter="inputEnter" ref="pageInput">页
        </span>
    </div>

</template>

<script>
    export default {
        props: {
            page: {
                type: Number,
                default: 0
            },
            totalPage: {
                type: Number,
                default: 0
            },
            count: {
                required: false,
                type: Number
            },
            pageNumber: {
                type: Number,
                default: 9
            }
        },
        computed: {
            info() {
                const pageCount = this.pageNumber
                const side = Math.floor((pageCount - 2 - 1) / 2)
                let start = this.page - side
                let end = this.page + side
                if (start < 2) {
                    start = 2
                    end = 1 + (pageCount - 2)
                }
                if (end > this.totalPage - 1) {
                    end = this.totalPage - 1
                    start = this.totalPage - (pageCount - 2)
                }
                return {
                    start, end
                }
            }
        },
        methods: {
            inputEnter() {
                const input = this.$refs.pageInput
                input.blur()
            },

            changeInputPage() {
                const input = this.$refs.pageInput
                let page = parseInt(input.value, 10)
                if (isNaN(page) || page === this.page) {
                    input.value = this.page
                    return
                }
                if (page < 1) {
                    page = 1
                    input.value = 1
                } else if (page > this.totalPage) {
                    page = this.totalPage
                    input.value = this.totalPage
                }
                this.$emit('onPageChange', page)
            },

            onPageChange(page) {
                page = parseInt(page, 10);
                if (!isNaN(page) && page >= 1 && page <= this.totalPage && page !== this.page) {
                    this.$emit('onPageChange', page)
                }
            }
        }
    };
</script>