/**
 * @file 数字处理
 */

// 格式化浮点型
export function formatFloat(float, bit) {
    if (isNaN(float)) {
        return ''
    }
    bit = (typeof(bit) !== 'number' ? 2 : bit)
    const accuracy = Math.pow(10, bit)
    const num = Math.round(float * accuracy) / accuracy
    if (bit <= 0) {
        return Math.floor(num) + ''
    }
    const sep = (num + '').split('.')
    const intPart = sep[0]
    let floatPart = sep[1] || ''
    for (let i = floatPart.length; i < bit; i++) {
        floatPart += '0'
    }
    return intPart + '.' + floatPart
}

// 格式化百分比
export function formatPercent(float, bit) {
    const num = formatFloat(float * 100, bit || 2)
    return num ? num + '%' : num
}