/**
 * @file 通用方法mixin
 */

export default {

    methods: {
        redirect(path, query = {}) {
            this.$router.push({path, query})
        },

        getQuery(key, defaultValue = null) {
            const query = this.$route.query
            if (typeof(query) !== 'object' || query[key] === undefined) {
                return defaultValue
            }
            return query[key]
        }
    }

}