<style lang="less">
</style>

<template>
    <div class="content-heading" v-if="path && path.length">
        <el-breadcrumb separator-class="el-icon-arrow-right" style="font-size: 15px">
            <el-breadcrumb-item v-for="nav in path" :key="nav.url">{{nav.text}}</el-breadcrumb-item>
        </el-breadcrumb>
    </div>
</template>

<script>
    import {NAV_LIST} from '../../conf/app'
    import _ from 'lodash'

    export default {
        computed: {
            path() {
                const currentUrl = this.$route.path
                const findNode = subs => {
                    let nodes = []
                    _.each(subs, sub => {
                        if (sub.url === currentUrl) {
                            nodes.push(sub)
                            return false
                        }
                        if (sub.sub) {
                            const child = findNode(sub.sub)
                            if (child.length > 0) {
                                nodes.push(sub)
                            }
                            nodes = _.union(nodes, child)
                        }
                    })
                    return nodes
                }
                return findNode(NAV_LIST)
            }
        }
    }
</script>