<style lang="less">
    .app-chart-box {
        width: 100%;
        height: 100%;
    }
</style>

<template>
    <div class="app-chart" :style="{width, height}">
        <div class="app-chart-box" :id="id"></div>
        <c-event :dom="window" @resize="resize" :lazy="500"></c-event>
    </div>
</template>

<script>
    export default {
        props: {
            option: {
                type: Object,
                default: null
            },
            width: {
                type: String,
                default: '400px'
            },
            height: {
                type: String,
                default: '300px'
            }
        },

        data() {
            const id = `app-chart-${this._uid}`
            return {
                id,
                instance: null,
                window: window,
                domSize: ''
            }
        },

        created() {
            this.$watch('option', this.draw)
            this.$watch('width', this.resize)
            this.$watch('height', this.resize)
        },

        mounted() {
            this.draw()
            this.domSize = this.getDomSize()
        },

        destroyed() {
            this.dispose()
        },

        methods: {
            getDomSize() {
                const dom = document.getElementById(this.id)
                if (dom !== null) {
                    return dom.offsetWidth + ',' + dom.offsetHeight
                }
                return ''
            },

            draw(clear = false) {
                require.ensure(['echarts'], () => {
                    if (this.instance === null) {
                        const dom = document.getElementById(this.id)
                        if (dom === null) {
                            return
                        }
                        const echarts = require('echarts')
                        this.instance = echarts.init(dom)
                    }
                    if (clear === true || this.option === null || this.option === undefined) {
                        this.instance.clear()
                    }
                    if (this.option !== null) {
                        // console.log('option', this.option)
                        this.instance.setOption(this.option)
                    }
                }, 'echarts')
            },

            dispose() {
                if (this.instance !== null) {
                    // console.log('dispose', this.id)
                    this.instance.dispose()
                }
            },

            resize() {
                if (this.instance === null) {
                    return
                }
                // 如果dom元素的尺寸没改变（如固定尺寸），则不触发echarts resize
                const domSize = this.getDomSize()
                if (this.instance !== null && domSize !== this.domSize && domSize !== '0,0') {
                    // console.log('resize', this.id)
                    this.instance.resize()
                    this.domSize = domSize
                }
            }
        }
    }
</script>