<style lang="less">
    .app-left-nav {
        border-right: none !important;
    }

    .no-animation {
        .horizontal-collapse-transition {
            transition: 0s width ease-in-out, 0s padding-left ease-in-out, 0s padding-right ease-in-out;
        }

        .collapse-transition {
            transition: 0s height ease-in-out, 0s padding-top ease-in-out, 0s padding-bottom ease-in-out;
        }
    }
</style>

<template>
    <el-menu :class="className" :default-active="activeIndex" text-color="#515253" :collapse-transition="false"
             active-text-color="#5d9cec" :router="true" :collapse="isCollapse" :unique-opened="true">
        <template v-for="item in navList">
            <el-submenu :index="item.url" :key="item.url" v-if="item.showSub && item.sub && isAuth(item)">
                <template slot="title">
                    <i class="fa fa-fw" :class="item.icon"></i>
                    &nbsp;&nbsp;
                    <span class="nav-label">{{item.text}}</span>
                </template>

                <template v-for="subItem in item.sub">
                    <el-submenu :index="subItem.url" :key="subItem.url" v-if="subItem.showSub && subItem.sub">
                        <template slot="title">
                            <i class="fa fa-fw"></i>
                            <span class="nav-label">{{subItem.text}}</span>
                        </template>
                        <el-menu-item v-for="subItem2 in subItem.sub" :index="subItem2.url" :route="getQuery(subItem2)"
                                      :key="subItem2.url">
                            <i class="fa fa-fw"></i>
                            <span class="nav-label">{{subItem2.text}}</span>
                        </el-menu-item>
                    </el-submenu>
                    <el-menu-item v-else :index="subItem.url" :route="getQuery(subItem)"
                                  :key="subItem.url">
                        <i class="fa fa-fw"></i>
                        <span class="nav-label">{{subItem.text}}</span>
                    </el-menu-item>
                </template>
            </el-submenu>
            <el-menu-item :index="item.url" :key="item.url" v-else-if="isAuth(item)">
                <i class="fa fa-fw" :class="item.icon"></i>
                &nbsp;&nbsp;
                <span slot="title" class="nav-label">{{item.text}}</span>
            </el-menu-item>
        </template>
    </el-menu>
</template>

<script>
    import {NAV_LIST} from '../../conf/app'
    import {APP_ENGLISH_TITLE} from '../../conf/app'
    import {mapGetters} from 'vuex'

    export default {
        props: {
            isCollapse: {
                type: Boolean,
                default: false
            }
        },

        computed: {
            ...mapGetters(['baseInfoState']),
            activeIndex() {
                return this.$route.path
            }
        },

        data() {
            return {
                navList: NAV_LIST,
                englishName: APP_ENGLISH_TITLE,
                className: 'app-left-nav no-animation'
            }
        },

        created() {
        },

        mounted() {
            setTimeout(() => {
                this.className = 'app-left-nav'
            }, 100)
        },

        methods: {
            getQuery(item) {
                return {
                    path: item.url
                }
            },

            isAuth(item) {
                return !item.auth || this.baseInfoState.auth.indexOf(item.auth) > -1
            }
        }
    }
</script>